body {
    margin: 0;
}



* {
  font-family: "Verdana", cursive, sans-serif;
}

.app {
  background-color: #f3f3f3;
  min-width: 450px;
  min-height: 200px;
  height: -webkit-min-content;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin-top: 100px;
  color: #252525;
}

.app.dark {
  background-color: #252525;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  color: #fafafa;
}

.score-section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  font-size: 20px;
  align-items: center;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
}

.score-section>p:first-of-type{
  grid-column: 1/3;
  text-align: center;
  font-size: 22px;
  font-weight: 800;
}

.category {
  margin-bottom: 5px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

}

.categoryResult {
  margin-top: 0;
  margin-bottom: 0px;
  color: #54e654;
}

.categoryName {
  margin-bottom: 2px;
  font-weight: 600;
}

.good {
  color: green;
}

.risk {
  color: rgb(238, 172, 51);
}

.bad {
  color: red;
}

.question-section {
  width: 100%;
}

.question-count {
  margin-bottom: 20px;
}

.question-count span {
  font-size: 28px;
}

.question-text {
  margin-bottom: 12px;
}

.answer-section {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

button {
  width: 48%;
  font-size: 16px;
  color: #ffffff;
  background-color: #252525;
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: space-around;
  align-items: center;
  border: 5px solid #3a3a3a;
  cursor: pointer;
}

button.dark {
  color: #ffffff;
  background-color: #252525;
  border: 5px solid #3a3a3a;
}

.correct {
  background-color: #2f922f;
}

.incorrect {
  background-color: #ff3333;
}

button:hover {
  background-color: #555e7d;
}

button:focus {
  outline: none;
}

button svg {
  margin-right: 5px;
}

header {
    position: fixed;
    top: 0;
    left: 0;
    height: 80px;
    background-color: rgb(238, 238, 238);
    width: 100vw;
    padding-left: 5vw;
    padding-right: 5vw;
    display: flex;
    align-items: center;
    box-shadow: 5px 10px 8px 0px rgba(0, 0, 0, 0.24);
}

header.dark {
    background-color: rgb(36, 36, 36); 
    color: rgb(247, 247, 247);
}

.toggler {
    position: inherit;
    right: 50px;
    display: flex;
    align-items: center;
}

.toggler p {
    font-size: 18px;
    font-weight: 600;
}

.toggler--slider {
    width: 30px;
    height: 13px;
    background-color: #2B283A;
    border-radius: 2rem;
    margin-inline: 7px;
    cursor: pointer;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border: 1px solid black;
    box-sizing: content-box;
}

header.dark .toggler--slider {
    border-color: white;
    background-color: #F5F5F5;
}

.toggler--slider--circle {
    height: 13px;
    width: 13px;
    background-color: white;
    border-radius: 50%;
}

header.dark .toggler--slider--circle {
    background-color: black;
}

.toggler--light {
    color: #2B283A;
}

header.dark .toggler--light {
    color: #757479;
}

.toggler--dark {
    color: #c7c7c7;
}

header.dark .toggler--dark {
    color: #FFFFFF;
}

header.dark .toggler--slider {
    justify-content: flex-end;
}
.main--container {
    background-image: url(/health_test/static/media/bgi-light.dfb27999.jpg);
    background-repeat: repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.main--container.dark {
    background-image: url(/health_test/static/media/bgi-dark.84baaedf.jpg);
}
